	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>-1-</strong>\n夏も大分暮れて来た幻想郷。 連日のように襲い掛かってきた猛暑もかなり和らいでいた。 そんな中、霊夢は神社で退屈そうに暇をつぶしていた。 もう妖霧も大雨もやって来ない。 とりあえず今日はもう1杯お茶飲んだらもう昼食の支度にしようかと立ち上がった瞬間、1匹の妖精が背後から突如襲いかかってきた。 ……が、決着は一瞬で片付いた。 なんてことない、相手は唯の雑魚妖精だった。 こんなことは蚊に刺されるのと同じ位日常的にあることだが、霊夢はここに非日常を見つけてしまった。 その妖精は霊夢にやられて消滅する瞬間、確かに紅魔館のメイド服を着ているのが見えてしまった。 普通メイド妖精が紅魔館から出ることはありえない。 出るのは咲夜くらいである…あれはまぁ人間だが。 ……今、紅魔館でまた何か異変が起きている……。 そう直感した霊夢は怖いもの見たさで館の方角に飛び立った。</p>\n<h2>-2-</h2>\n<p>幻想郷の奥地に存在する魔法の森。 ここは冬は寒いがそれ以外は暑くも無く寒くも無く、常にそこそこの温度の風が吹いて迷い込んだ者を惑わす。 そんな中、魔理沙は自宅で全自動で暇を潰す符の製作に打ち込んでいた。 もう妖霧も大雨もやって来ないがこの森ではさほど影響は無い。 そう、彼女もまた暇なのであった。 そうだ、あの図書館を忘れていた……あそこの魔導書をいくつかくすねてくれば製作がはかどるかもしれないぜ。 ふと黒い心が沸いて来た外見も黒い少女は、早速魔力箒に跨り館を目指し飛び立った。 だが、湖の先には館などどこにも見えなかった。 ただ湖の上で数多の紅魔館のメイド妖精が暴れまわっていた。</p>","frontmatter":{"cover":null,"date":"2004-07-09T03:46:58+09:00","strDate":"2004/7/8","title":"ストーリー(後付け)"}}},"pathContext":{"path":"/ja/blog/2004/07/09-ストーリー後付け/","langKey":"ja"}}

